<template>
  <div class="tetris-game">
    <!-- Í≤åÏûÑ ÏãúÏûë Ïò§Î≤ÑÎ†àÏù¥ -->
    <div v-if="gameState.status === 'ready'" class="game-overlay start-overlay">
      <div class="message-box">
        <h2>TETRIS</h2>
        <p>PRESS START</p>
        <button @click="startGame" class="start-button">START</button>
      </div>
    </div>

    <!-- Í≤åÏûÑ Ïò§Î≤Ñ Ïò§Î≤ÑÎ†àÏù¥ -->
    <div v-if="gameState.status === 'gameover'" class="game-overlay end-overlay">
      <div class="message-box">
        <h2>GAME OVER</h2>
        <p>SCORE: {{ score }}</p>
        <button @click="startGame" class="restart-button">RESTART</button>
      </div>
    </div>

    <!-- Í∏∞Ï°¥ Í≤åÏûÑ Ïª®ÌÖêÏ∏† -->
    <div class="tetris">
      <div class="stars-effect"></div>
      
      <div class="background">
        <div class="text-container" aria-hidden="true">
          <span v-for="n in 20" :key="n">Hannim</span>
        </div>
      </div>
      
      <div class="game-layout">
        <!-- Î©îÏù∏ Í≤åÏûÑ Ï∫îÎ≤ÑÏä§ -->
        <div class="canvas-container">
          <canvas ref="gameBoard" width="300" height="600"></canvas>
          <div class="neon-border"></div>
        </div>

        <!-- Ïö∞Ï∏° Ï†ïÎ≥¥ Ìå®ÎÑê -->
        <div class="info-panel">
          <!-- Ïä§ÏΩîÏñ¥Î≥¥Îìú -->
          <div class="score-board">
            <div class="digital-display">
              <div class="display-label">SCORE</div>
              <div class="display-value">{{ String(score).padStart(6, '0') }}</div>
            </div>
          </div>

          <!-- Îã§Ïùå Î∏îÎ°ù ÎØ∏Î¶¨Î≥¥Í∏∞ -->
          <div class="next-piece">
            <div class="preview-label">NEXT</div>
            <canvas ref="nextPieceCanvas" width="120" height="120"></canvas>
            <div class="neon-border"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
