  }
  
  .tetris-game .info-panel {
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }
}

.tetris-game .game-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
