}

.tetris-game .message-box {
  text-align: center;
  color: #00FFFF;
  padding: 2rem;
  border: 2px solid #00FFFF;
  background: rgba(0, 0, 0, 0.9);
  box-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
