      0 0 20px #00FFFF,
      0 0 30px #00FFFF,
      0 0 40px #00FFFF;
  }
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes messageAppear {
