}
</style>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';

// ÏÉÅÏàò Ï†ïÏùò
const BLOCK_SIZE = 30;
const BOARD_WIDTH = 10;
const BOARD_HEIGHT = 20;

// Î∏îÎ°ù Ï†ïÏùòÎ•º Í∞ùÏ≤¥Î°ú Î≥ÄÍ≤Ω
const SHAPES = {
  I: {
    shape: [[1, 1, 1, 1]],
    color: '#00f0f0'  // Î∞ùÏùÄ ÏãúÏïà
  },
  O: {
    shape: [[1, 1], 
            [1, 1]],
    color: '#f0f000'  // Î∞ùÏùÄ ÎÖ∏Îûë
  },
  T: {
    shape: [[1, 1, 1],
            [0, 1, 0]],
    color: '#a000f0'  // Î≥¥Îùº
  },
  L: {
    shape: [[1, 1, 1],
            [1, 0, 0]],
    color: '#f0a000'  // Ï£ºÌô©
  },
  J: {
    shape: [[1, 1, 1],
            [0, 0, 1]],
    color: '#0000f0'  // ÌååÎûë
  },
  S: {
    shape: [[0, 1, 1],
            [1, 1, 0]],
    color: '#00f000'  // Ï¥àÎ°ù
  },
  Z: {
    shape: [[1, 1, 0],
            [0, 1, 1]],
    color: '#f00000'  // Îπ®Í∞ï
  }
};

// Î∞òÏùëÌòï ÏÉÅÌÉú Ï†ïÏùò
const gameBoard = ref(null);
const ctx = ref(null);
const score = ref(0);
const gameInterval = ref(null);

// Í≤åÏûÑ ÏÉÅÌÉú
const gameState = ref({
  board: Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0)),
  currentPiece: null,
  currentPieceX: 0,
  currentPieceY: 0,
  status: 'ready' // 'ready', 'playing', 'gameover'
});

// Îã§Ïùå Î∏îÎ°ù ÏÉÅÌÉú Ï∂îÍ∞Ä
const nextPiece = ref(null);
const nextPieceCanvas = ref(null);

// Í≤åÏûÑ ÔøΩÔøΩÏ†ï ÏÉÅÏàò Ï∂îÍ∞Ä
const INITIAL_SPEED = 1000; // Ï¥àÍ∏∞ ÏÜçÎèÑ (1Ï¥à)
const MIN_SPEED = 100;      // ÏµúÏÜå ÏÜçÎèÑ (0.1Ï¥à)
const SPEED_FACTOR = 0.9;   // ÏÜçÎèÑ Ï¶ùÍ∞Ä ÎπÑÏú®

// ÌòÑÏû¨ Í≤åÏûÑ ÏÜçÎèÑ ÏÉÅÌÉú Ï∂îÍ∞Ä
const gameSpeed = ref(INITIAL_SPEED);

// ÌòÑÏû¨ Î∏îÎ°ùÏùò ÏÉâÏÉÅÏùÑ Ï†ÄÏû•Ìï† ÏÉÅÌÉú Ï∂îÍ∞Ä
const currentColor = ref('');
const nextColor = ref('');

const PLACED_BLOCK_COLOR = '#006666'; // Î∞îÎã•Ïóê ÎÜìÏù∏ Î∏îÎ°ùÏùò Ïñ¥ÎëêÏö¥ Ï≤≠Î°ùÏÉâ

// Í≤åÏûÑ Í¥ÄÎ†® Î©îÏÑúÎìúÎì§
const createPiece = () => {
  if (nextPiece.value === null) {
    const pieces = Object.keys(SHAPES);
    const randomPiece = pieces[Math.floor(Math.random() * pieces.length)];
    nextPiece.value = SHAPES[randomPiece].shape;
    nextColor.value = SHAPES[randomPiece].color;
  }
  
  gameState.value.currentPiece = nextPiece.value;
  currentColor.value = nextColor.value;
  
  gameState.value.currentPieceX = Math.floor(BOARD_WIDTH / 2) - Math.floor(gameState.value.currentPiece[0].length / 2);
  gameState.value.currentPieceY = 0;
  
  const pieces = Object.keys(SHAPES);
  const randomPiece = pieces[Math.floor(Math.random() * pieces.length)];
  nextPiece.value = SHAPES[randomPiece].shape;
  nextColor.value = SHAPES[randomPiece].color;
  
  drawNextPiece();
};

// collision Ìï®Ïàò ÏàòÏ†ï
const collision = (x = gameState.value.currentPieceX, 
                  y = gameState.value.currentPieceY, 
                  piece = gameState.value.currentPiece) => {
  for (let r = 0; r < piece.length; r++) {
    for (let c = 0; c < piece[r].length; c++) {
      if (piece[r][c]) {
        const newX = x + c;
        const newY = y + r;
        
        if (newX < 0 || newX >= BOARD_WIDTH || newY >= BOARD_HEIGHT) {
          return true;
        }
        
        if (newY >= 0 && gameState.value.board[newY][newX]) {
          // Î∏îÎ°ùÏù¥ Îß® ÏúÑÏóêÏÑú Ï∂©ÎèåÌïòÎ©¥ Í≤åÏûÑ Ïò§Î≤Ñ
          if (newY === 0) {
            gameOver();
            return true;
          }
          return true;
        }
      }
    }
  }
  return false;
};

const getGhostPosition = () => {
  let ghostY = gameState.value.currentPieceY;
  
  while (!collision(gameState.value.currentPieceX, ghostY + 1, gameState.value.currentPiece)) {
    ghostY++;
  }
  
  return ghostY;
};

// drawBoard Ìï®Ïàò ÏàòÏ†ï
const drawBoard = () => {
  if (!ctx.value) return;
  
  // Î∞∞Í≤Ω Ï¥àÍ∏∞Ìôî
  ctx.value.fillStyle = 'black';
  ctx.value.fillRect(0, 0, gameBoard.value.width, gameBoard.value.height);

  // Î≥¥ÎìúÏùò Î∏îÎ°ù Í∑∏Î¶¨Í∏∞ (Ïù¥ÎØ∏ ÎÜìÏó¨ÏßÑ Î∏îÎ°ù)
  for (let y = 0; y < BOARD_HEIGHT; y++) {
    for (let x = 0; x < BOARD_WIDTH; x++) {
      if (gameState.value.board[y][x]) {
        // Î©îÏù∏ Î∏îÎ°ù Í∑∏Î¶¨Í∏∞
        ctx.value.fillStyle = PLACED_BLOCK_COLOR;
        ctx.value.fillRect(
          x * BLOCK_SIZE, 
          y * BLOCK_SIZE, 
          BLOCK_SIZE - 1, 
          BLOCK_SIZE - 1
        );
        
        // Î∏îÎ°ùÏùò ÏúÑÏ™ΩÍ≥º ÏôºÏ™ΩÏóê ÌïòÏù¥ÎùºÏù¥Ìä∏ Ìö®Í≥º
        ctx.value.fillStyle = '#008080';
        ctx.value.fillRect(
          x * BLOCK_SIZE, 
          y * BLOCK_SIZE, 
          BLOCK_SIZE - 1, 
          2
        );
        ctx.value.fillRect(
          x * BLOCK_SIZE, 
          y * BLOCK_SIZE, 
          2, 
          BLOCK_SIZE - 1
        );
        
        // Î∏îÎ°ùÏùò ÏïÑÎûòÏ™ΩÍ≥º Ïò§Î•∏Ï™ΩÏóê Í∑∏Î¶ºÏûê Ìö®Í≥º
        ctx.value.fillStyle = '#004d4d';
        ctx.value.fillRect(
          x * BLOCK_SIZE, 
          (y + 1) * BLOCK_SIZE - 2, 
          BLOCK_SIZE - 1, 
          2
        );
        ctx.value.fillRect(
          (x + 1) * BLOCK_SIZE - 2, 
          y * BLOCK_SIZE, 
          2, 
          BLOCK_SIZE - 1
        );
      }
    }
  }

  // ÌòÑÏû¨ Îñ®Ïñ¥ÏßÄÎäî Î∏îÎ°ù Í∑∏Î¶¨Í∏∞
  if (gameState.value.currentPiece) {
    // 1000Ï†ê ÎØ∏ÎßåÏùº ÎïåÎßå Í∑∏Î¶ºÏûê ÌëúÏãú
    if (score.value < 1000) {
      const ghostY = getGhostPosition();
      ctx.value.fillStyle = `${currentColor.value}40`; // 40Îäî ÏïåÌååÍ∞í (25% Ìà¨Î™ÖÎèÑ)
      for (let y = 0; y < gameState.value.currentPiece.length; y++) {
        for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
          if (gameState.value.currentPiece[y][x]) {
            ctx.value.fillRect(
              (gameState.value.currentPieceX + x) * BLOCK_SIZE,
              (ghostY + y) * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
          }
        }
      }
    }

    // ÌòÑÏû¨ Î∏îÎ°ù Í∑∏Î¶¨Í∏∞ (Îñ®Ïñ¥ÏßÄÎäî Ï§ëÏù∏ Î∏îÎ°ù)
    ctx.value.fillStyle = currentColor.value;
    for (let y = 0; y < gameState.value.currentPiece.length; y++) {
      for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
        if (gameState.value.currentPiece[y][x]) {
          // Î©îÏù∏ Î∏îÎ°ù Í∑∏Î¶¨Í∏∞
          ctx.value.fillRect(
            (gameState.value.currentPieceX + x) * BLOCK_SIZE,
            (gameState.value.currentPieceY + y) * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
          
          // ÎÑ§Ïò® Ìö®Í≥º
          ctx.value.shadowColor = currentColor.value;
          ctx.value.shadowBlur = 5;
          ctx.value.fillRect(
            (gameState.value.currentPieceX + x) * BLOCK_SIZE,
            (gameState.value.currentPieceY + y) * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
          ctx.value.shadowBlur = 0;
        }
      }
    }
  }
};

// mergePiece Ìï®Ïàò ÏàòÏ†ï
const mergePiece = () => {
  for (let y = 0; y < gameState.value.currentPiece.length; y++) {
    for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
      if (gameState.value.currentPiece[y][x]) {
        const boardY = gameState.value.currentPieceY + y;
        const boardX = gameState.value.currentPieceX + x;
        gameState.value.board[boardY][boardX] = PLACED_BLOCK_COLOR; // Í≥†Ï†ïÎêú ÏÉâÏÉÅ ÏÇ¨Ïö©
      }
    }
  }
};

// Ï†êÏàò ÏóÖÎç∞Ïù¥Ìä∏ Î∞è ÏÜçÎèÑ Ï°∞Ï†à Ìï®Ïàò
const updateScore = (clearedLines) => {
  const previousThousand = Math.floor(score.value / 1000);
  score.value += clearedLines * 100;
  const currentThousand = Math.floor(score.value / 1000);
  
  // 1000Ï†ê Îã®ÏúÑÎ°ú ÏÜçÎèÑ Ï¶ùÍ∞Ä
  if (currentThousand > previousThousand) {
    // ÏµúÏÜå ÏÜçÎèÑÎ•º ÎÑòÏßÄ ÏïäÎèÑÎ°ù Ï†úÌïú
    const newSpeed = Math.max(
      MIN_SPEED,
      INITIAL_SPEED * Math.pow(SPEED_FACTOR, currentThousand)
    );
    
    // Í≤åÏûÑ Ïù∏ÌÑ∞Î≤å Ïû¨ÏÑ§Ï†ï
    if (gameInterval.value) {
      clearInterval(gameInterval.value);
      gameInterval.value = setInterval(gameLoop, newSpeed);
      gameSpeed.value = newSpeed;
    }
  }
};

// checkLines Ìï®Ïàò ÏàòÏ†ï
const checkLines = () => {
  let linesToRemove = [];
  
  // ÏôÑÏÑ±Îêú ÎùºÏù∏ Ï∞æÍ∏∞
  for (let y = BOARD_HEIGHT - 1; y >= 0; y--) {
    if (gameState.value.board[y].every(cell => cell)) {
      linesToRemove.push(y);
    }
  }
  
  if (linesToRemove.length > 0) {
    // ÎùºÏù∏ Ï†úÍ±∞ Ïï†ÎãàÎ©îÏù¥ÏÖò
    let flash = 0;
    const flashInterval = setInterval(() => {
      flash++;
      
      for (const y of linesToRemove) {
        const color = flash % 2 === 0 ? '#FFFFFF' : '#FFFF00';
        for (let x = 0; x < BOARD_WIDTH; x++) {
          ctx.value.fillStyle = color;
          ctx.value.fillRect(
            x * BLOCK_SIZE,
            y * BLOCK_SIZE,
            BLOCK_SIZE - 1,
            BLOCK_SIZE - 1
          );
        }
      }
      
      if (flash >= 4) {
        clearInterval(flashInterval);
        
        // ÎùºÏù∏ Ï†úÍ±∞ Î∞è Ï†êÏàò Í≥ÑÏÇ∞
        linesToRemove.forEach(y => {
          gameState.value.board.splice(y, 1);
          gameState.value.board.unshift(Array(BOARD_WIDTH).fill(0));
        });
        
        // Ï†êÏàò ÏóÖÎç∞Ïù¥Ìä∏ Î∞è ÏÜçÎèÑ Ï°∞Ï†à
        updateScore(linesToRemove.length);
        drawBoard();
      }
    }, 100);
  }
};

const hardDrop = () => {
  if (gameState.value.status !== 'playing') return;
  
  while (!collision()) {
    gameState.value.currentPieceY++;
  }
  gameState.value.currentPieceY--;
  mergePiece();
  checkLines();
  createPiece();
  if (collision()) {
    gameOver();
    return;
  }
  drawBoard();
};

// Í≤åÏûÑ Ïò§Î≤Ñ Ìï®Ïàò ÏàòÏ†ï
const gameOver = () => {
  if (gameInterval.value) {
    clearInterval(gameInterval.value);
    gameInterval.value = null;
  }
  gameState.value.status = 'gameover';
  drawBoard(); // ÎßàÏßÄÎßâ ÏÉÅÌÉú Í∑∏Î¶¨Í∏∞
};

// gameLoop Ìï®Ïàò ÏàòÏ†ï
const gameLoop = () => {
  if (gameState.value.status !== 'playing') return;
  
  gameState.value.currentPieceY++;
  if (collision()) {
    gameState.value.currentPieceY--;
    mergePiece();
    checkLines();
    createPiece();
    if (collision()) {
      gameOver();
      return;
    }
  }
  drawBoard();
};

// Í≤åÏûÑ ÏãúÏûë Ìï®Ïàò ÏàòÏ†ï
const startGame = () => {
  gameState.value = {
    board: Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0)),
    currentPiece: null,
    currentPieceX: 0,
    currentPieceY: 0,
    status: 'playing'
  };
  score.value = 0;
  gameSpeed.value = INITIAL_SPEED;
  nextPiece.value = null;
  createPiece();
  if (gameInterval.value) clearInterval(gameInterval.value);
  gameInterval.value = setInterval(gameLoop, gameSpeed.value);
  drawBoard();
};

// ÌÇ§ ÏûÖÎ†• Ï≤òÎ¶¨ Ìï®Ïàò ÏàòÏ†ï
const handleKeydown = (e) => {
  // Ïä§ÌéòÏù¥Ïä§Î∞î Ïä§ÌÅ¨Î°§ Ìï≠ÏÉÅ Î∞©ÏßÄ
  if (e.key === ' ') {
    e.preventDefault();
  }
  
  // Í≤åÏûÑÏù¥ ÏßÑÌñâ Ï§ëÏù¥ ÏïÑÎãàÎ©¥ Ïó¨Í∏∞ÏÑú Ï¢ÖÎ£å
  if (gameState.value.status !== 'playing') {
    return;
  }
  
  // Îã§Î•∏ ÌÇ§ ÏûÖÎ†•ÎèÑ Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
  e.preventDefault();
  
  switch (e.key) {
    case 'ArrowLeft':
      gameState.value.currentPieceX--;
      if (collision()) gameState.value.currentPieceX++;
      break;
    case 'ArrowRight':
      gameState.value.currentPieceX++;
      if (collision()) gameState.value.currentPieceX--;
      break;
    case 'ArrowDown':
      gameState.value.currentPieceY++;
      if (collision()) gameState.value.currentPieceY--;
      break;
    case 'ArrowUp': {
      const rotated = gameState.value.currentPiece[0].map((_, i) => 
        gameState.value.currentPiece.map(row => row[i]).reverse()
      );
      const previousPiece = gameState.value.currentPiece;
      gameState.value.currentPiece = rotated;
      if (collision()) gameState.value.currentPiece = previousPiece;
    }
      break;
    case ' ':
      hardDrop();
      break;
  }
  
  drawBoard();
};

// Îã§Ïùå Î∏îÎ°ù Í∑∏Î¶¨Í∏∞ Ìï®Ïàò
const drawNextPiece = () => {
  if (!nextPieceCanvas.value || !nextPiece.value) return;
  
  const ctx = nextPieceCanvas.value.getContext('2d');
  const blockSize = 25;
  
  ctx.clearRect(0, 0, nextPieceCanvas.value.width, nextPieceCanvas.value.height);
  
  const offsetX = (nextPieceCanvas.value.width - nextPiece.value[0].length * blockSize) / 2;
  const offsetY = (nextPieceCanvas.value.height - nextPiece.value.length * blockSize) / 2;
  
  ctx.fillStyle = nextColor.value;
  for (let y = 0; y < nextPiece.value.length; y++) {
    for (let x = 0; x < nextPiece.value[y].length; x++) {
      if (nextPiece.value[y][x]) {
        ctx.fillRect(
          offsetX + x * blockSize,
          offsetY + y * blockSize,
          blockSize - 1,
          blockSize - 1
        );
        
        // ÎÑ§Ïò® Ìö®Í≥º
        ctx.shadowColor = nextColor.value;
        ctx.shadowBlur = 10;
        ctx.fillRect(
          offsetX + x * blockSize,
          offsetY + y * blockSize,
          blockSize - 1,
          blockSize - 1
        );
        ctx.shadowBlur = 0;
      }
    }
  }
};

// ÏÉâÏÉÅÏùÑ Ïñ¥Îë°Í≤å ÎßåÎìúÎäî Ìó¨Ìçº Ìï®Ïàò
const shadeColor = (color, percent) => {
  const num = parseInt(color.replace('#', ''), 16);
  const amt = Math.round(2.55 * percent);
  const R = (num >> 16) + amt;
  const G = (num >> 8 & 0x00FF) + amt;
  const B = (num & 0x0000FF) + amt;
  return `#${(
    0x1000000 +
    (R < 255 ? (R < 1 ? 0 : R) : 255) * 0x10000 +
    (G < 255 ? (G < 1 ? 0 : G) : 255) * 0x100 +
    (B < 255 ? (B < 1 ? 0 : B) : 255)
  ).toString(16).slice(1)}`;
};

// ÎùºÏù¥ÌîÑÏÇ¨Ïù¥ÌÅ¥ ÌõÖ
onMounted(() => {
  ctx.value = gameBoard.value.getContext('2d');
  // Ï†ÑÏó≠ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑàÎ°ú Î≥ÄÍ≤Ω
  window.addEventListener('keydown', handleKeydown, { passive: false });
  gameState.value.status = 'ready';
  drawBoard();
});

onUnmounted(() => {
  if (gameInterval.value) clearInterval(gameInterval.value);
  window.removeEventListener('keydown', handleKeydown);
});
</script>